.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-tap-highlight-color: transparent;
}
*,
:after,
:before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style-type: none;
  -webkit-padding-start: 0;
  padding-inline-start: 0;
}
input::-ms-clear {
  display: none;
}
a,
button {
  display: inline-block;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  font: inherit;
  text-decoration: none;
  text-align: left;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
:root {
  /* Common Theme */
  --color-font-dark: #2d2d2d;
  --color-font-light: #ffffff;
  --color-back-light: #ffffff;
  --color-back-dark: #2d2d2d;
  --color-back-img: #2d2d2d;
  --color-special: #102f28;
  --color-bright: #c73d42;
  --color-ultra-back: #000000;
  --color-blur: rgba(0, 0, 0, 0.5);
  --color-card-blur: rgba(0, 0, 0, 0.7);
  --color-card-blur-hover: rgba(0, 0, 0, 0.8);
  --color-success: #47AD4C;
  --color-error: #FF0505;
  /* Light Theme */
  --color-font-basic: var(--color-font-dark);
  --color-back-basic: var(--color-back-light);
  --color-btn-basic: var(--color-special);
  --color-border-brand: rgba(45, 45, 45, 0.5);
  --color-back-brand: #f4f4f4;
  --color-border-input: rgba(45, 45, 45, 0.5);
  --color-back-footer: #2d2d2d;
}
/* Dark Theme */
.dark-theme {
  --color-font-basic: var(--color-font-light);
  --color-back-basic: #0F0F0F;
  --color-btn-basic: #ffffff;
  --color-border-brand: #070707;
  --color-back-brand: #0a0a0a;
  --color-border-input: rgba(255, 255, 255, 0.5);
  --color-back-footer: #000000;
}
