.cross-btn {
  position: relative;
  width: 32px;
  height: 32px;
}
.cross-btn::before,
.cross-btn::after {
  position: absolute;
  content: "";
  top: 0;
  left: calc((32px - 2px) / 2);
  width: 2px;
  height: 32px;
  margin: 0 auto;
  background-color: currentColor;
}
.cross-btn::before {
  transform: rotate(45deg);
}
.cross-btn::after {
  transform: rotate(-45deg);
}
@media (min-width: 1200px) {
  .cross-btn--adaptive {
    position: relative;
    width: 48px;
    height: 48px;
  }
  .cross-btn--adaptive::before,
  .cross-btn--adaptive::after {
    position: absolute;
    content: "";
    top: 0;
    left: calc((48px - 2px) / 2);
    width: 2px;
    height: 48px;
    margin: 0 auto;
    background-color: currentColor;
  }
  .cross-btn--adaptive::before {
    transform: rotate(45deg);
  }
  .cross-btn--adaptive::after {
    transform: rotate(-45deg);
  }
}
.cross-btn:focus-visible {
  outline: 1px solid var(--color-bright);
  outline-offset: 3px;
}
.blur {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: var(--color-blur);
  opacity: 0;
  pointer-events: none;
}
.blur--active {
  opacity: 1;
  pointer-events: auto;
}
.modal {
  position: fixed;
  inset: 0;
  z-index: 99;
  display: flex;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  pointer-events: none;
  visibility: hidden;
}
.modal:focus {
  outline: none;
}
.modal::-webkit-scrollbar-button {
  background-color: transparent;
}
.modal--opened {
  pointer-events: auto;
  visibility: visible;
  -webkit-overflow-scrolling: touch;
}
.modal__wrapper {
  position: relative;
  margin: auto;
  transition: transform 0.5s, opacity 0.5s;
  transform: translateY(110%);
  opacity: 0;
}
.modal--opened .modal__wrapper {
  transform: translateY(0);
  opacity: 1;
}
