.card__link {
  position: relative;
  width: 100%;

  color: var(--color-font-light);
  background-color: var(--color-back-img);

  &::before {
    position: absolute;
    content: "";
    inset: 0;
    z-index: 1;

    width: 100%;
    height: 100%;
    background-color: var(--color-card-blur);
    opacity: .5;
  }
}

.card__title {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  z-index: 1;

  .font-title();
  font-size: 29px;
  text-align: center;

  transform: translateY(-50%);
  transition: transform .5s, top .5s, margin .5s;

  @media (min-width: @max-content) {
    top: 0;
    margin-top: 50%;
    font-size: 40px;

    transform: translateY(0);
  }
}

.card__link:hover .card__title,
.card__link:focus-visible .card__title {
  top: 40px;
  margin-top: 0;

  transform: translate(0);

  @media (min-width: @xl-width) {
    top: 65px;
  }
}

.card__description {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  z-index: 1;

  display: flex;
  flex-direction: column;
  gap: 5px;
  align-items: center;

  .font-basic();
  text-align: center;

  transform: translateY(-50%);
  opacity: 0;
  transition: opacity .5s;
}

.card__link:hover .card__description,
.card__link:focus-visible .card__description {
  opacity: 1;
}

.card__span {
  .font-basic-sm-bold();
  text-align: center;
}

.card__img {
  overflow: hidden;
}

.card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  transform: scale(1);
  transition: transform .5s;
}

.card__link:hover .card__img img,
.card__link:focus-visible .card__img img {
  transform: scale(1.1);
}
