.page-footer {
  color: var(--color-font-light);
  background-color: var(--color-back-footer);
}

.page-footer__wrapper {
  .wrapper();
  padding-top: 40px;
  padding-bottom: 40px;

  @media (min-width: @xl-width) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

.page-footer__top {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-bottom: 71px;

  @media (min-width: @xl-width) {
    flex-direction: row;
    column-gap: 70px;
    row-gap: 25px;
    flex-wrap: wrap;
    justify-content: space-between;

    margin-bottom: 49px;
  }
}

.page-footer__logo {
  flex-shrink: 0;
  margin-bottom: 52px;

  @media (min-width: @xl-width) {
    margin-bottom: 0;
  }
}

.page-footer__site-list {
  margin-bottom: 55px;

  @media (min-width: @xl-width) {
    margin-bottom: 0;
  }
}

.page-footer__social-list {
  margin-bottom: 55px;

  @media (min-width: @lg-width) {
    order: 1;
    margin-bottom: 0;
  }

  @media (min-width: @xl-width) {
    display: none;
  }
}

.page-footer__connection {
  flex-shrink: 0;

  @media (min-width: @lg-width) {
    margin-bottom: 55px;
  }

  @media (min-width: @xl-width) {
    margin-bottom: 0;
    margin-left: auto;
  }
}

.page-footer__call-btn {
  color: currentColor;
}

.page-footer__copyright {
  text-align: center;
}
